@import "variables";


.selector{
    position: absolute;
    top: 1rem;
    left: 4.5rem;

    &__wrapper{
        position: relative;
        left: -4.5rem;
        top: 0.5rem;
        width: 100vw;
    }

    &__box{
        margin: auto;
        width: 80vw;
        min-width: 18.75rem;
        height: 100%;
        border: 2px solid $inactiveColor;
        border-radius: 1rem;
        background: $activeColor;
        padding: 1rem;
        -webkit-box-shadow: 0px 7px 27px 12px #320D3E; 
        box-shadow: 0px 7px 27px 12px #320D3E;

        &-underline{
            width: 80%;
            margin-left: 10%;
        }

    }

    &__input{
       height: 1rem;
       min-width: 10rem;
       width: 80%;
       padding: 1rem;
       border-radius: 1rem;
       border: 2px solid $fontColor;
       margin: 0.5rem auto;
       color: $fontColor;
       background: $bgColor;

       &:focus{
           background: $bgColorInactive;
           outline: none;
       }
    }

    &__select{
        position: relative;
        min-width: 4rem;
        width: 20%;
        padding: 0.5rem 1rem;
        border-radius: 1rem;
        border: 2px solid $fontColor;
        color: $fontColor;
        background: $bgColor;

        &-selected{
            color: black;
        }
        
    }

}

#productName{
    display: block;
}

#productAmount{
    min-width: 5rem;
    width: 40%;
    margin-right: 5%;
}