@import "variables";

.btn{
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    color: $bgColor;
    font-size: 20px;

    &-active{
        border: 2px solid $fontColor; 
        background: $bgColor;
        color: $fontColor
    }

    &-inactive{
        border:0; 
        background: $inactiveColor;
    }
}