@import "variables";

.item{
    background-color: $activeColor;
    padding: 0.3rem;
    margin-bottom: 0.3rem;
    min-width: 16rem;
    border-radius: 2rem;
    color: $bgColor;

    &-inactive{
        background: $bgColorInactive;
        color: $fontColor;
    }

    &__info{
        margin-left: 0.5rem;
    }

    &__input{
        height: 1rem;
        min-width: 10rem;
        padding: 0.8rem;
        width: 25vw;
        min-width: 4.5rem;
        border-radius: 1rem;
        border: 2px solid $fontColor;
        margin: 0 0.5rem;
        color: $fontColor;
        background: $bgColor;
        animation: showUp 2s forwards;
     }


    &__btn{
        &-right{
            float: right;
            margin-right: 0.4rem;
        }
    }
}