@import "variables";

.history{
    margin-left: 8%;
    padding-top: 2rem;
    width: 80%;

    &__btn{
    height: 3rem;
    border-radius: 1.5rem;
    border: 2px solid $fontColor; 
    color: $fontColor;
    padding: 0 1rem;
    margin-left: 0.5rem;
    margin-bottom: 0.5rem;
    background: $bgColor;
    }

    & h3{
        color: $bgColor;
        margin-left: 1rem;
        text-shadow: -1px -1px 0 $fontColor, 1px -1px 0 $fontColor, -1px 1px 0 $fontColor, 1px 1px 0 $fontColor;
        font-weight: 900;
    margin-bottom: 1rem;
    }
}